.loading {
  background: center no-repeat;

  position: fixed;
  left: 0;
  top: 0;
  z-index: 88;
  width: 100vw;
  background-color: rgb(255, 255, 255);
  height: 100%;
  display: flex;
  color: white;
  font-weight: 800;

  align-items: center;
  justify-content: center;
  font-size: 80px;
  font-family: sans-serif;
  transition: all 0.5s ease-in-out;
}
.loadingimg {
  width: 100px;
  height: 100px;
}
